<template>
    <div class="agent">
        <div class="content">
            <div class="list-header">
                <div class="list-title">
                    <span>代理列表</span>
                    <div class="list-operate">
                        <i-input v-model="searchKey" class="columns-item-input" placeholder="用户ID/用户名"></i-input>
                        <div class="search" @click="getData">搜索</div>
                        <div class="poptip">
                            <p>1.玩家输入房号的时候填入代理推荐码则自动绑定上下级关系</p>
                            <!--<p>2.代理由会员管理页面转换过来。取消代理后，其账号则回到会员管理列表</p>-->
                        </div>
                    </div>
                </div>
            </div>
            <div class="list-data">
                <div class="data-header">
                    <p class="item-info">头像</p>
                    <p class="item-info">用户名</p>
                    <p class="item-info">ID</p>
                    <p class="item-info">账户余额</p>
                    <p class="item-info">
                        <span>专属房间号</span>
                        <span>（代理推荐码）</span>
                    </p>
                    <p class="item-info">操作</p>
                </div>
                <div class="data-con">
                    <div class="data-item" v-for="(item, index) in agentData" :key="index">
                        <div class="item-info">
                            <img :src="item.header" alt title/>
                        </div>
                        <div class="item-info">{{item.username}}</div>
                        <div class="item-info">{{item.showId}}</div>
                        <div class="item-info">{{item.balance}}</div>
                        <div class="item-info">{{item.roomNum}}</div>
                        <div class="item-info operate">
                            <div class="item-button" @click="handleQueryOffline(item)">下线管理</div>
                            <div class="item-button" @click="handleAddOffline(item)">添加下线</div>
                            <div class="item-button" @click="handleShowCancel(item, 'agent')">取消代理</div>
                            <div class="item-button" @click="handleModifyBackwater(item)">回水管理</div>
                        </div>
                    </div>
                </div>
            </div>
            <Page class="footer-page" :total="page.total" :page-size="page.pageSize" :current="page.pageNum" show-sizer @on-page-size-change="handleChangeSize" @on-change="handleChangePage"/>
        </div>
        <div v-show="backwaterShow || addOfflineShow || offlineListShow" class="agent-mask">
            <div v-show="backwaterShow" class="agent-back-water">
                <div class="back-water-header">
                    <span>设置代理回水比例</span>
                    <span class="modal-close" @click="handleCloseModal('backwaterShow')">X</span>
                </div>
                <div class="back-water-list">
                    <div class="back-water-item" v-for="(item, index) in gameList" :key="index">
                        <p class="back-water-item-h">{{`${item.title}回水`}}</p>
                        <InputNumber :max="100" :min="0" v-model="item.value" class="columns-item-input">
                        </InputNumber>
                        <span>%</span>
                    </div>
                    <div class="back-water-item">
                        <i-button @click="handleSubmit">提交修改</i-button>
                    </div>
                </div>
            </div>
            <div v-show="addOfflineShow" class="add-offline">
                <div class="add-offline-header">
                    <span>添加下线</span>
                    <span class="modal-close" @click="handleCloseModal('addOfflineShow')">X</span>
                </div>
                <div class="add-offline-list">
                    <div class="add-offline-item">
                        <p class="add-offline-item-h">登录账号</p>
                        <!--<i-input v-model="addOffline.username" class="columns-item-input"></i-input>-->
                        <Select v-model="addOffline.username" filterable class="columns-item-input">
                            <Option v-for="(item, index) in showUserList" :value="item.id" :key="index">{{ item.nickName }}</Option>
                        </Select>
                    </div>
                    <div class="add-offline-item">
                        <i-button @click="handleAddOfflineSub">添加</i-button>
                    </div>
                </div>
            </div>
            <div v-show="offlineListShow" class="offline-list">
                <div class="offline-list-header">
                    <span>下线列表</span>
                    <span class="modal-close" @click="handleCloseModal('offlineListShow')">X</span>
                </div>
                <div class="offline-list-table">
                    <i-table :columns="offlineListCols" :data="offlineListData" height="370"></i-table>
                </div>
            </div>
        </div>
        <Modal v-model="showDelete" :styles="{top: '200px'}" width="300">
            <p slot="header" style="color: #0d1941;font-size: 16px;text-align:center">
                <span>确认取消</span>
            </p>
            <p style="text-align:center">是否确认取消？</p>
            <div slot="footer" class="modal-footer">
                <div class="enter-cancel" @click="showDelete = false">取消</div>
                <div class="enter-ok" @click="handleEnterCancel">确定</div>
            </div>
        </Modal>
        <!-- <loading :loading="showLoading"></loading> -->
    </div>
</template>

<script>
export default {
    data() {
        let that = this;
        return {
            page: {
                total: 0,
                pageSize: 10,
                pageNum: 1
            },
            showLoading: false,
            searchKey: '',
            backwaterShow: false,
            addOfflineShow: false,
            offlineListShow: false,
            showDelete: false,
            addOffline: {
                username: ''
            },
            gameList: [],
            offlineListCols: [
                {
                    title: '用户信息',
                    align: 'center',
                    key: 'userInfo'
                },
                {
                    title: '操作',
                    align: 'center',
                    render: (h, params) => {
                        return h('div', [
                            h('Button', {
                                props: {
                                    type: 'primary',
                                    size: 'small'
                                },
                                style: {
                                    marginRight: '5px'
                                },
                                on: {
                                    click: () => {
                                        that.handleShowCancel(params.row, 'offline');
                                    }
                                }
                            }, '取消下线')
                        ]);
                    }
                }
            ],
            offlineListData: [],
            backwaterId: '',
            backwater: {
                id: '',
                userId: '',
                type: ''
            },
            agentData: [],
            isLoading: false,
            userList: [],
            showUserList: []
        }
    },
    mounted() {
        let that = this;

        that.getGameList().then(function() {
            that.getData();
            that.getUserList();
        })
    },
    methods: {
        getGameList() {
            let that = this;

            return new Promise((resolve, reject) => {
                that.$axios({
                    url: '/api/Merchant/GetGameList'
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        const result = data.data.Data;

                        that.gameList = result.map(item => {
                            return {
                                title: item.GameName,
                                type: item.GameType,
                                key: item.NickName,
                                value: 0
                            }
                        }).sort((a, b) => a.type - b.type);
                        resolve();
                    }
                    reject();
                })
            })
        },
        handleChangeSize(_pageSize) {
            let that = this;

            that.page.pageSize = _pageSize;
            that.page.pageNum = 1;
            that.getData();
        },
        handleChangePage(page) {
            let that = this;

            that.page.pageNum = page;
            that.getData();
        },
        handleEnterCancel() {
            let that = this;

            switch(that.backwater.type) {
                case 'agent':
                    that.handleCancelAgent();
                break;
                case 'offline':
                    that.handleCancelOffline();
                break;
            }
        },
        handleShowCancel(item, type) {
            let that = this;

            that.backwater.id = item.id;
            that.backwater.userId = item.userId;
            that.backwater.type = type;
            that.showDelete = true;
        },
        handleCancelAgent() {
            let that = this;

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/User/AvatarOperation',
                    params: {
                        userID: that.backwater.userId
                    }
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                    that.isLoading = false;
                    that.showDelete = false;
                    if (that.page.total % that.page.pageSize === 1 && that.page.total !== 1) {
                        that.page.pageNum = parseInt(that.page.total / that.page.pageSize);
                    }

                    if (that.page.total === 1) {
                        that.page.pageNum = 1;
                    }
                    that.getData();
                })
            }
        },
        handleModifyBackwater(item) {
            let that = this;
            that.backwaterShow = true;
            that.getBackwaterById(item.id);
        },
        getBackwaterById(vid) {
            let that = this;

            that.$axios({
                url: '/api/AgentBackwater/GetAgentInfo',
                params: {
                    id: vid
                }
            }).then(function(data) {
                if (data.data.Status === 100) {
                    const result = data.data.Model;

                    that.backwaterId = result.ID;
                    that.gameList.forEach(function(item) {
                        item.value = result[item.key];
                    })
                }
            })
        },
        handleSubmit() {
            let that = this,
                vParam = {};

            if (!that.isLoading) {
                that.isLoading = true;
                vParam['ID'] = that.backwaterId;
                that.gameList.forEach(function(item) {
                    vParam[item.key] = item.value;
                })

                that.$axios({
                    url: '/api/AgentBackwater/UpdateAgentInfo',
                    method: 'put',
                    data: vParam
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                    that.isLoading = false;
                    that.backwaterShow = false;
                    that.getData();
                })
            }
        },
        handleAddOffline(item) {
            let that = this;
            that.backwater.id = item.id;
            that.showUserList = that.userList.filter(opt => {return opt.id !== item.userId});
            that.addOfflineShow = true;
        },
        handleAddOfflineSub() {
            let that = this;

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/AgentBackwater/AddOfflineUser',
                    method: 'put',
                    params: {
                        id: that.backwater.id,
                        userID: that.addOffline.username
                    }
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }

                    that.isLoading = false;
                    that.addOfflineShow = false;
                    that.getData();
                })
            }
        },
        handleQueryOffline(item) {
            let that = this;
            that.backwater.id = item.id;
            that.getOfflineList();
            that.offlineListShow = true;
        },
        getOfflineList() {
            let that = this;

            that.$axios({
                url: '/api/AgentBackwater/GetOfflineUsers',
                params: {
                    id: that.backwater.id
                }
            }).then(function(data) {
                if (data.data.Status === 100) {
                    const result = data.data.Data;

                    that.offlineListData = result.map(item => {
                        let res = that.userList.find(ele => ele.id === item.UserID);
                        return {
                            id: item.ID,
                            userId: item.UserID,
                            userInfo: item.NickName + (res ? ('(' + res.onlyCode + ')') : '')
                        }
                    })
                }
            })
        },
        handleCloseModal(key) {
            let that = this;
            that[key] = false;
        },
        handleCancelOffline(item) {
            let that = this;

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/AgentBackwater/DeleteOfflineUser',
                    method: 'delete',
                    params: {
                        id: that.backwater.id,
                        userID: that.backwater.userId
                    }
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                    that.isLoading = false;
                    that.showDelete = false;
                    that.offlineListShow = false;
                    that.getData();
                })
            }
        },
        getData() {
            let that = this,
                vParam = {};

            if (that.searchKey) {
                vParam['keyword'] = that.searchKey;
            }

            vParam['start'] = that.page.pageNum;
            vParam['pageSize'] = that.page.pageSize;

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/AgentBackwater/GetAgentUserList',
                    params: vParam
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        const result = data.data.Data;

                        that.page.total = data.data.Total;
                        that.agentData = result.map(item => {
                            return {
                                id: item.ID,
                                header: item.Avatar ? (item.Avatar.indexOf('://') === -1 ? ('/' + item.Avatar) : item.Avatar) : '',
                                username: item.NickName,
                                loginName: item.LoginName,
                                balance: item.UserMoney,
                                racingRatio: item.Racing + '%',
                                airshipRatio: item.Airship + '%',
                                timeHonoredRatio: item.TimeHonored + '%',
                                speedRacerRatio: item.ExtremeSpeed + '%',
                                austrTenRatio: item.Aus10 + '%',
                                austrFiveRatio: item.Aus5 + '%',
                                roomNum: item.RoomNum,
                                userId: item.UserID,
                                isAgent: item.IsAgent,
                                showId: item.OnlyCode
                            }
                        })
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                    that.isLoading = false;
                })
            }
        },
        getUserList() {
            let that = this;

            that.$axios({
                url: '/api/Setup/GetUpDownUserList',
                params: {
                    status: false
                }
            }).then(function(data) {
                if (data.data.Status === 100) {
                    const result = data.data.Data;

                    that.userList = result.map(item => {
                        return {
                            id: item.UserID,
                            onlyCode: item.OnlyCode,
                            nickName: item.NickName + '(' + item.OnlyCode + ')'
                        }
                    })
                }
                that.isLoading = false;
            })
        }
    },
}
</script>

<style lang="scss" scoped>
.agent {
    position: relative;
    .content {
        .list-header {
            .list-title {
                justify-content: space-between;

                width: 100%;
                .list-operate {
                    display: flex;
                    align-items: center;

                    font-weight: 400;

                    .columns-item-input {
                        width: 140px;
                    }

                    .search {
                        margin-left: 13px;

                        width: 106px;
                        height: 28px;
                        line-height: 24px;

                        // background-color: rgb(255, 255, 255);
                        // border-radius: 4px;
                        // border: solid 1px rgb(102, 102, 102);
                        background-color: #0078ff;
                        box-shadow: inset 0px 0px 5px 0px rgba(124, 183, 255, 0.75);
                        border-radius: 10px;
                        border: solid 1px #0e3c90;
                        text-align: center;
                        cursor: pointer;
                        color: #fff;
                    }

                    .poptip {
                        flex-shrink: 0;

                        margin-left: 60px;
                        margin-right: 15px;

                        width: 460px;

                        color: rgb(102, 102, 102);

                        & > p {
                            line-height: 14px;
                        }

                        & > p:last-child {
                            margin-top: 5px;
                        }
                    }
                }
            }
        }

        .list-data {
            margin-top: 20px;

            border: solid 1px #bcbcbc;

            .data-header, .data-item {
                display: flex;
                align-items: stretch;

                height: 50px;

                background-color: #e4e4e4;

                .item-info {
                    width: 58px;
                    line-height: 50px;

                    border-right: solid 1px #d7d7d7;

                    & > img {
                        width: 36px;
                        height: 36px;

                        vertical-align: middle;
                    }
                }

                .item-info:nth-child(2), .item-info:nth-child(5) {
                    width: 132px;
                }

                .item-info:nth-child(5) {
                    display: flex;
                    align-items: center;
                    flex-direction: column;
                    justify-content: center;

                    line-height: 20px;
                }

                .item-info:nth-child(3) {
                    width: 125px;

                    overflow: hidden;
                }

                .item-info:nth-child(4) {
                    width: 142px;
                }

                .item-info:nth-child(6) {
                    width: 339px;

                    border-right: 0;
                }

                .operate {
                    display: flex;
                    align-items: center;
                    justify-content: space-around;

                    .item-button {
                        width: 73px;
                        height: 27px;
                        line-height: 23px;

                        // background-color: rgb(255, 153, 51);
                        // border-radius: 4px;
                        // border: solid 1px rgb(102, 102, 102);
                        background-color: #fd8c00;
                        box-shadow: inset 0px 0px 5px 0px rgba(255, 241, 124, 0.75);
                        border-radius: 10px;
                        border: solid 1px #906d0e;
                        cursor: pointer;
                        color: #fff;
                    }

                    .item-button:nth-child(2){
                        background-color: #148f5f;
                        box-shadow: inset 0px 0px 5px 0px rgba(124, 255, 173, 0.75);
                        border-radius: 10px;
                        border: solid 1px #0e9051;
                    }

                    .item-button:nth-child(3) {
                        background-color: #fd8c00;
                        box-shadow: inset 0px 0px 5px 0px rgba(255, 241, 124, 0.75);
                        border-radius: 10px;
                        border: solid 1px #906d0e;
                    }

                    .item-button:last-child {
                        background-color: #312c8f;
                        box-shadow: inset 0px 0px 5px 0px rgba(127, 124, 255, 0.75);
                        border-radius: 10px;
                        border: solid 1px #0e2390;
                    }
                }
            }

            .data-con {
                min-height: 550px;

                overflow: auto;
                .data-item {
                    height: 40px;

                    background-color: #fff;
                    .item-info {
                        line-height: 40px;

                    }
                }

                .data-item:nth-child(2n+2) {
                    background-color: #f9f9f9;
                }
            }
        }
    }

    .agent-mask {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;

        width: 100%;
        height: 100%;

        background-color: rgba(55,55,55,.6);

        z-index: 1000;

        .agent-back-water, .add-offline, .offline-list {
            position: fixed;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;

            margin: auto;

            width: 400px;
            height: 430px;

            border: solid 3px #cdcdcd;
            border-radius: 4px;

            background-color: #f2f2f2;

            z-index: 1001;

            .back-water-header, .add-offline-header, .offline-list-header {
                display: flex;
                justify-content: space-between;

                padding: 5px;

                background-color: #ddd;

                font-weight: bold;

                .modal-close {
                    cursor: pointer;
                }
            }

            .back-water-list, .add-offline-list, .offline-list-table {
                margin: 10px;

                border: solid 1px #ddd;
                border-radius: 4px;

                .back-water-item, .add-offline-item {
                    display: flex;
                    justify-content: space-between;

                    padding: 10px 0;

                    border-bottom: solid 1px #ddd;

                    .back-water-item-h, .add-offline-item-h {
                        display: flex;
                        align-items: center;
                        justify-content: flex-end;

                        margin-right: 20px;

                        width: 130px;
                    }

                    .columns-item-select, .columns-item-input {
                        position: relative;

                        margin-right: 10px;
                        
                        width: 190px;
                    }

                    & > span:last-child {
                        display: inline-block;

                        margin-right: 10px;

                        width: 20px;
                        height: 32px;
                        line-height: 32px;
                    }
                }

                .back-water-item:last-child {
                    justify-content: center;

                    border: none;
                }

                .add-offline-item:last-child {
                    justify-content: center;

                    border: none;
                }
            }
        }

        .agent-back-water {
            height: 530px;
        }

        .add-offline {
            height: 170px;
        }
    }
}
</style>